body {
  width: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(135deg, #fdfcfb 0%, #e2d1c3 100%);}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.characterList {
  margin: 75px 200px;
  width: calc(100% - 400px);
}

.characterList-header {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 30px;
  gap: 30px;
  margin-bottom: 30px;
  a {
    font-size: 20px;
    font-weight: 700;
    color: white;
  }
}
.characterList-header > a {
  font-size: 20px;
  font-weight: 700;
  color: black;
}

.characterList-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  grid-gap: 5px;
  gap: 5px;
}

.character {
  display: flex;
}
.characterContent {
  margin-left: 2rem;
}
.characterEpisode {
  margin-top: 2rem;
}

.search {
  margin: 75px 200px;
  width: calc(100% - 400px);
}

.search-header {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 30px;
  gap: 30px;
  margin-bottom: 30px;
  a {
    font-size: 20px;
    font-weight: 700;
    color: white;
  }
}
.search-header > a {
  font-size: 20px;
  font-weight: 700;
  color: black;
}

.search-area {
  display: flex;
  align-items: center;
  justify-content: center;
}

.search-area > input {
  height: 32px;
  min-width: 400px;
}
.search-area > button {
  height: 38px;
  min-width: 50px;
  background-color: green;
  color: white;
  font-size: 16px;
}

